import React from 'react';
import { Button,
  Header, Container, Grid } from 'semantic-ui-react';
import Testimonial from './Testimonial.jsx';

const TESTIMONIALS = [
  {
    quote: 'Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Quisque ac nunc a justo tincidunt volutpat eget vel nisi. Nunc consectetur condimentum tincidunt. Lorem ipsum dolor sit amet, consectetur adipiscing elit.',
    author: 'Jane Doe'
  },
  {
    quote: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Dolor, alias unde incidunt odio natus. Dolor explicabo vitae fugiat minus ipsum magnam minima dolores corrupti iste eveniet recusandae illum temporibus, id facere assumenda modi itaque, deserunt animi, veritatis unde sequi quam.',
    author: 'Magyar Petra'
  },
  {
    quote: 'Sed eu hendrerit libero. Nulla pharetra erat non ex tempor, eget fermentum metus condimentum. Proin posuere sed lorem egestas facilisis. Nunc id interdum tortor.',
    author: 'Pintér Kata'
  },
  {
    quote: 'Cras non ullamcorper libero. Sed sed feugiat ante. Curabitur nec mauris a sapien lobortis posuere. Sed eu felis velit. Nullam et sodales elit. Fusce ut viverra velit. ',
    author: 'Karen Smith'
  },
  {
    quote: 'Mauris tempus ipsum ex, sit amet tincidunt diam laoreet lobortis. Donec molestie nisl eros, a luctus eros laoreet elementum. In hac habitasse platea dictumst. Suspendisse congue laoreet tincidunt. Nunc a nulla consequat, convallis erat a, molestie metus.',
    author: 'Péntek Júlia'
  },
  {
    quote: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Fugiat officiis vitae, maiores unde earum commodi quia nesciunt voluptas, odit nostrum nulla, enim accusamus expedita hic nobis a. Perferendis, explicabo, dolorem.',
    author: 'Kolozsvári László'
  }
];

export default class Home extends React.Component {
  render() {
    const testimonials = [];
    const sliderSettings = {
      dots: true,
      infinite: true,
      speed: 3000,
      slidesToShow: 1,
      slidesToScroll: 1
    };

    for (var i = 0; i < 3; i++) {
      const props = {
        quote: TESTIMONIALS[i].quote,
        author: TESTIMONIALS[i].author
      };

      if (true) {
        props.active = true;
      }

      testimonials.push(<Grid.Column key={ i }>
                          <Testimonial {...props} />
                        </Grid.Column>);
    }

    return <section id='testimonials'>
             <Header as='h2' className='heading'>Pácienseim mondták</Header>
             <div className='testimonial-cards'>
               <Container>
                 <Grid stackable columns={ 3 }>
                   { testimonials }
                 </Grid>
               </Container>
             </div>
           </section>;
  }
}



